import React from 'react';
import PropTypes from 'prop-types';

const VASPackagesPresentationComponent = props => {
    const vasesTitles = getVasesTitles(filterVasesByCategories(props.vases, "GratisPackageBonuses"));
    const changePackagesButtonLabel = vasesTitles.length === 1 ? props.changePackageSingularLabel : props.changePackagePluralLabel;
    return vasesTitles.length > 0 ? (
        <p className={props.className || ""}>{vasesTitles.length === 1 ? props.singularLabel : props.pluralLabel} {vasesTitles.join(", ")}
            {props.editable &&
            <a className="u-spacing-left" href="#" key='vasPackagesChangeButton_key'
                onClick={preventDefaultWrapper(props.onVasPackagesChangeClicked)}
                aria-label={changePackagesButtonLabel.toLowerCase()}>
                {changePackagesButtonLabel}
            </a>}
        </p>) : null;
};

const preventDefaultWrapper = handler => event => {
    event.preventDefault();
    handler(event);
};

const filterVasesByCategories = (vases, category) =>
    vases && category && vases.filter(vas => vas.categories && vas.categories.includes(category)) || [];

const getVasesTitles = vases => vases && vases.map(vas => vas.name) || [];

VASPackagesPresentationComponent.propTypes = {
    editable: PropTypes.bool,
    onVasPackagesChangeClicked: PropTypes.func,
    vases: PropTypes.array,
    bundleCode: PropTypes.string,
    singularLabel: PropTypes.string,
    pluralLabel: PropTypes.string,
    changePackageSingularLabel: PropTypes.string,
    changePackagePluralLabel: PropTypes.string,
    className: PropTypes.string,
};

VASPackagesPresentationComponent.defaultProps = {
    singularLabel: "Wybrana paczka usług: ",
    pluralLabel: "Wybrane paczki usług: ",
    changePackageSingularLabel: "Zmień paczkę",
    changePackagePluralLabel: "Zmień paczki",
};

export default VASPackagesPresentationComponent;
